@mixin desktop {
    @media (max-width: 1200px) { 
        @content;
     }
  }

@mixin tablet-lg {
    @media (max-width: 992px) { 
        @content;
     }
  }

  @mixin tablet {
    @media (max-width: 768px) { 
        @content;
     }
  }

@mixin mobile {
    @media (max-width: 576px) { 
        @content;
     }
  }

  @mixin mobile-sm {
    @media (max-width: 375px) { 
        @content;
     }
  }

  @mixin not-last-child($selector) {
    & #{$selector}:not(:last-child) {
      @content;
    }
  }